.txt-editor {
    i {
        width: 25px;
        height: 25px;
        border: 1px solid black;
        padding: 5px;
        font-size: 14px;
        margin: 5px;

        &:hover {
            background-color: black;
            color: white;
        }

        &.active {
            background-color: black;
            color: white;
        }
    }
}

.renderInput.textfield {
    i {
        display: none;
    }

    &:hover {
        i {
            display: flex;
        }
    }
}