  padding: 10px;
  margin-top: 10px;
  background: #ccc;
}

.sideBar,
.configMenu,
.configDrawer {
  max-width: 100%;
  color: grey;
}

.sideBar {
  // flex-direction: column;
  // width: max-content;
  padding: 10px;
  // border-right: 1px solid #111;
  height: inherit;
  position: relative;
}

.sm {
  .sideBar {
    max-width: 350px;
  }
}

.page {
  // flex: 1 1 auto;
  padding: 0 20px;
  height: inherit;
}

.dropZone {
  // display: none;
  flex: 0 0 auto;
  height: 10px;
  //   width: 40px;
  transition: 200ms all;
  z-index: 102;
  //   position: absolute;
  //   right: 0;
  //   margin-left: -40px;
  // background-color: yellow;
  //  right: 0;
}

.dropZone-column,
.dropZone-column-left {
  display: block;
  height: 100%;
  width: 100%;
  transition: 200ms all;
  position: absolute;
  left: 0;

  //   margin-left: -40px;
  // background-color: rgba(0, 0, 255, 0.377);
  //  right: 0;
  &.isLast {
    display: block;
  }
}

.dropComponent {
  // background-color: rgba(255, 255, 0, 0.336);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  z-index: auto;
}

.dropZone.horizontalDrag {
  position: absolute;
  width: 20px;
  height: 100%;
  //   height: 40px;
  top: 0px;
  left: -10px;
  z-index: 0;
  // background-color: yellow;
}

.list,
.ls {
  cursor: pointer;
  scroll-margin: 150px;
  padding: 10px;
  width: 100%;
  // display: flex;
  // flex-wrap: nowrap !important;
  // overflow-x: auto !important;
  justify-content: unset !important;

  -webkit-overflow-scrolling: touch;

  @media screen and (max-width: 700px) {
    &::-webkit-scrollbar {
      display: none;
    }
  }

  // &.draggable {
  //   display: grid;
  // }

  .scrollButton {
    position: absolute;
    display: none;
    cursor: pointer;

    top: 50%;
    z-index: 150;

    transition: transform 0.1s ease-in-out;

    background: white;
    border-radius: 100%;
    border: none;
    padding: 0rem;
    width: 20px;
  }
}

.scrollButton {
  position: absolute;
  display: none;
  cursor: pointer;

  top: 50%;
  z-index: 150;

  transition: transform 0.1s ease-in-out;

  background: white;
  border-radius: 100%;
  border: none;
  padding: 0rem;
  width: 20px;
  &.left{
    left: 0px
  }
  &.right{
    right: 0px
  }
}

.builder-type,
.builder-selector {
  position: absolute;
  width: fit-content;
  // min-width: 50px;
  // max-width: 350px;
  bottom: -20px;
  height: 20px;
  left: 0;
  align-items: center;
  background-color: $green;
  margin-top: 9px;
  z-index: 200;
  display: none;
  color: white;
  text-align: center;
  padding: 5px;
  font-size: 10px!important;
  i {
    margin-right: 5px;
    margin-left: 5px;
    font-size: 12px;
  }
}

.mainEl {
  &.active>.builder-type {
    display: flex;
    padding: 0px;
    &:hover >*{ 
      transform: none!important;
    }
  }
  
}

.builder-selector {
  font-size: 12px;
  cursor: grab;

  &:active {
    cursor: grabbing;
  }
}


.component,
.column,
.grid,
.section,
.page,
.sideBar,
.leftsidebar,
.rightsidebar,
.navbar,
.header,
.footer,
.horizontalTab,
.bottomNavbar {
  overflow-x: hidden;
  overflow-y: hidden;

  // &.edit {
  //   border: 1px solid transparent;
  // }
  outline-offset: -2px;
  background-color: transparent;
  background-repeat: no-repeat;

  .horizontalDrag {
    &.left {
      display: none;
    }

    &.right {
      // display: none;
      width: 100%;
      height: 100%;
      top: 0px;
      bottom: 0px;
      left: 0px;
      right: 0px;
      // z-index: auto;
    }
  }

  .clickable {
    cursor: pointer;
  }

  &:hover {
    outline-offset: -1px;
    .dropZone.horizontalDrag {
      // display: none;
      z-index: 0;
    }
  }
}

.component{
  outline-offset: -2px;
  &.image.active{
    outline-offset: 0px;

  }
}
.leftsidebar, .rightsidebar{
  overflow-y: auto;
}
.row {
  --bs-gutter-x: 0;
}

.empty-block {
  min-height: 100px !important;
  min-width: 250px !important;
  z-index: 100;
}

.section {
  $grid-columns: 12 !default;
  min-height: 20px;
}

.component {
  overflow: hidden;

  &.active,
  &:hover {
    overflow: visible;
  }

  &.preview {
    overflow: hidden;
  }

  &.button {
    padding: 5px 10px;
    border-radius: 5px;
  }
}

.column,
.grid,
.section,
.horizontalTab {
  &.active {
    overflow: visible;

    &>div>.horizontalDrag {
      // z-index: -1;
    }
  }

  &:hover {
    overflow: visible;
  }

  &.preview {
    overflow: hidden;
  }
}
