  position: absolute;
  width: 100%;
  height: 100%;
  //   height: 40px;
  top: 0;
  right: -10px;
  z-index: 0;
  // background-color: yellow;
}

.dropColumn {
  // position: absolute;
  // background-color: rgba(234, 1, 255, 0.452);
  width: 100%;
  height: auto;
  margin-right: -100%;
  z-index: 1;
}


.dropZone:not(.horizontalDrag).isLast {
  flex: 1 1 auto;
}

.dropZone.active,
.trashDropZone.active {
  background: #3f414246;
  transition: 1000ms all;

  &.drop-page {
    background: rgba(73, 0, 128, 0.227);
  }

  &.drop-section {
    background: rgba(0, 128, 0, 0.275);
  }

  &.drop-grid {
    background: rgba(0, 174, 255, 0.329);
  }

  &.drop-column {
    background: rgba(255, 30, 0, 0.268);
  }

  // width: 50px;
  // position: relative;
}

.dropZone-column.active {
  background: #00a2ff69;
  transition: 1000ms all;
}

.dropZone-column.isLast {
  // background-color: rgba(255, 0, 0, 0.404);
  height: 100%;
  width: 100%;
  bottom: 0px;
  z-index: auto;
  position: absolute;

  &.active {
    background: #00a2ff69;
    transition: 1000ms all;
  }

}

.dragHandle {
  background-color: green;
  display: none;
  width: 30px;
  height: 30px;
  position: absolute;
  left: 0px;
  top: 45%;
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;

  &:active {
    cursor: -webkit-grabbing;
    cursor: -moz-grabbing;
    cursor: grabbing;

  }
}

.component,
.column,
.grid,
.section,
.page {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: transparent;
  align-content: start;
}

.cp,
.cl,
.grd,
.sct {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: transparent;
  align-content: baseline;
  display: flex;
  flex-wrap: wrap;
}

