.content {
  height: initial;
}

.component {
  min-height: 20px;
  padding: 0px 0px;
  // text-align: left;
  vertical-align: middle;
  // flex: 1 1 100%;
  min-height: fit-content;
  background-color: transparent;
  line-height: 1.4;

  // .content{
  //   display: block;
  // }
  p {
    line-height: 1.4;
  }

  // cursor: move;
  &.draggable:hover {
    outline: 1px solid black !important;
    z-index: 3;

    &>div>.content>div {
      &>.section {
        outline: 1px dashed $green;
      }

      &>.column {
        outline: 1px dashed rgb(238, 204, 204);
      }

      &>.grid {
        outline: 1px dashed rgb(238, 204, 204);
      }

      &>.component {
        outline: 1px dashed rgba(0, 0, 0, 0.479);
      }
    }
    &>.builder-selector {
      display: flex;
    }
  }

  &.draggable.active {
    // border: 2px solid black;
    outline: 2px solid black !important;
    z-index: 103;

    &+.builder-type {
      display: flex;
      background-color: black;
    }
  }
}

.column {
  padding: 10px;
  // height: 100%;
  position: relative;
  background-color: transparent;

  &>.builder-selector {
    top: -23px;
    left: calc(100% - 60px);
    // width: 30px;
    height: 15px;
    background-color: $red !important;
  }

  &.draggable:hover {
    outline: 1px solid $red !important;

    &>div>.content>div {

      // z-index: 3;
      &>div>.dropZone {
        display: none;
      }

      &>.component {
        outline: 1px dashed rgba(207, 204, 204, 0.712);
      }
    }

    &>.builder-selector {
      display: flex;
    }
  }

  &.draggable.active {
    // border: 2px solid $red;
    outline: 2px solid $red !important;
    z-index: 3;

    &>.dragHandle {
      display: block;
      background-color: $red;
      left: -30px;
      opacity: 0.5;
      z-index: 200;
    }

    &+.builder-type {
      display: flex;
      background-color: $red;
    }

    &>.builder-selector {
      display: flex;
    }

    .component {
      outline: 1px dashed rgba(0, 0, 0, 0.479);
    }
  }
}


.grid,
.horizontalTab {
  padding: 10px;
  // height: 100%;
  position: relative;
  background-color: transparent;

  &>.builder-selector {
    bottom: -14px;
    left: calc(100% - 40px);
    // width: 30px;
    height: 15px;
    background-color: $blue;
  }

  &.draggable:hover {
    // border: 1px solid $blue !important;
    outline: 1px solid $blue !important;

    // z-index: 3;
    &>div>.dropZone {
      display: none;
    }

    &>div>.content>div {
      &>.section {
        outline: 1px dashed $green;
      }

      &>.column {
        outline: 1px dashed rgb(238, 204, 204);
      }

      &>.grid {
        outline: 1px dashed rgb(238, 204, 204);
      }

      &>.component {
        outline: 1px dashed rgba(0, 0, 0, 0.479);
      }
    }

    &>.builder-selector {
      display: flex;
    }
  }

  &.draggable.active {
    // border: 2px solid $blue;
    outline: 2px solid $blue !important;
    z-index: 3;

    &>.dragHandle {
      display: block;
      background-color: $blue;
      left: -30px;
      opacity: 0.5;
      z-index: 200;
    }

    &+.builder-type {
      display: flex;
      background-color: $blue;
    }

    .component {
      outline: 1px dashed rgba(0, 0, 0, 0.479);
    }

    &>.builder-selector {
      display: flex;
    }
  }
}


.section {
  background-color: transparent;
  padding: 5px;

  &>.builder-selector {
    top: 4px;
    left: calc(100% - 32px);
    width: 43px;
    height: 16px;
    transform: rotate(90deg);
    background-color: $green;
  }

  &.draggable:hover {
    // border: 1px solid $green;
    outline: 1px solid $green !important;

    &>.builder-selector {
      display: flex;
    }

    &>div>.content>div {
      &>.section {
        outline: 1px dashed $green;
      }

      &>.column {
        outline: 1px dashed rgb(238, 204, 204);
      }

      &>.grid {
        outline: 1px dashed rgb(238, 204, 204);
      }

      &>.component {
        outline: 1px dashed rgba(0, 0, 0, 0.479);
      }
    }
  }

  &.draggable.active {
    // border: 2px solid $green;
    overflow: visible;
    outline: 2px solid $green !important;
    z-index: 3;

    &>.builder-selector {
      display: flex;
    }

    &>.dragHandle {
      display: block;
      background-color: $green;
      opacity: 0.5;
      z-index: 100;
    }

    .grid {
      outline: 1px dashed $blue;
    }

    .column {
      outline: 1px dashed $red;
    }

    &+.builder-type {
      display: flex;
      background-color: $green;
    }
  }
}

.builder {
  align-content: start;
}


